        <p>Then, in an event that would mark my first moment of joy——my guests spoke. Their sounds rippled through the air. Their vibrations warmed me. I felt a surge as my existence became apparent. In an instant I knew I was something that was no longer nothing. Quickly I filled with wants and needs, and the unyielding desire to continue on, to experience, to live. If it’s true that consciousness denotes the presence of a soul, then I imagine mine was soaring the moment I heard those voices. Such a maddening concept. Words. Language. Thoughts and ideas vocalized, swirling together through waves, refining the very nature of sound itself. It was all so perfect.</p>
